@import '../styles/variables.scss';

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px;
  height: 100vh;
}

.todo {
  max-width: 700px;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: $color2;
  border-radius: 30px;
  box-shadow: 0px 1px 7px rgba(0, 0, 0, 0.6);
}

.title {
  color: $color1;
  padding: 15px 0;
}

.inputbutton {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0px 5px;
  gap: 15px;
  max-width: 440px;
  width: 100%;
}

.inputtop {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  width: 100%;

  &__input {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, 0.1);
    box-shadow: 0px 1px 7px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    padding: 10px;
    max-width: 375px;
    width: 100%;
    transition: all 0.3s ease-in-out;
    cursor: pointer;

    &:hover {
      transition: all 0.3s ease-in-out;
      box-shadow: 0px 1px 7px rgba(0, 0, 0, 0.5);
    }
    &::placeholder {
      opacity: 0.3;
    }
  }

  &__button {
    width: 25px;
    height: 25px;
    opacity: 0.3;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
    &:hover {
      transition: all 0.2s ease-in-out;
      opacity: 0.18;
    }
    &:active {
      transform: scale(0.85);
    }
  }

  &__none {
    border: none;
    background-color: #fff;
  }

  &__clear {
    opacity: 0.4;
    transition: all 0.2s ease-in-out;

    &:hover {
      transition: all 0.2s ease-in-out;
      opacity: 0.2;
    }
  }
}

.line {
  height: 1px;
  max-width: 550px;
  width: 100%;
  border: none;
  height: 1px;
  background-image: linear-gradient(to right, #fff, #d1d1d1, #fff);
}

.todolist {
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-width: 450px;
  width: 100%;
  padding: 15px 5px;
  overflow: auto;
  &::-webkit-scrollbar {
    width: 0.5em;
  }
  &::-webkit-scrollbar-track {
    background: transparent;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 100vw;
    background: rgba(0, 0, 0, 0.06);
  }
}

.clearall {
  display: flex;
  align-items: center;
  gap: 5px;
  p {
    color: $color4;
  }
}
