@import '../../styles/variables.scss';

.todoitem {
  max-width: 430px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  box-shadow: 0px 1px 7px rgba(0, 0, 0, 0.5);
  padding: 10px;
  border-radius: 15px;
  border: transparent;

  &__button {
    width: 20px;
    height: 20px;
    opacity: 0.4;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
    &:hover {
      transition: all 0.2s ease-in-out;
      opacity: 0.2;
    }
    &:active {
      transform: scale(0.85);
    }
  }

  .buttoncontainer {
    display: flex;
    flex-direction: column;
    gap: 5px;
  }

  .done {
    transition: all 0.2s ease-in-out;
    text-decoration: line-through;
    opacity: 0.4;
  }

  .none {
    color: $color1;
  }
}
