@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import './variables.scss';

*,
*:after,
*:before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
  outline: none;
  text-decoration: none;
  overflow-anchor: none;
}

body {
  font-family: 'Roboto';
  background-color: $background;
}
